
@font-face {
    font-family: 'turkish_another_typewriterRg';
    src: url('/fonts/turkish_another_typewriter_regular-webfont.eot');
    src: url('/fonts/turkish_another_typewriter_regular-webfont.eot?#iefix') format('embedded-opentype'),
         url('/fonts/turkish_another_typewriter_regular-webfont.woff2') format('woff2'),
         url('/fonts/turkish_another_typewriter_regular-webfont.woff') format('woff'),
         url('/fonts/turkish_another_typewriter_regular-webfont.svg#turkish_another_typewriterRg') format('svg');
	font-weight: normal;
	font-style: normal;
}
body {
	font-family: 'turkish_another_typewriterRg' !important;
	font-weight: normal;
	font: 20px sans-serif;
}
.timeline--wrapper{
    padding: 0;
}
.timeline {
    margin: 50px auto 0;
    color: #61b8ff;
}
.entry .title:before {
    border: 3px solid;
    background-color: #61b8ff;
    top: 0;
    right: -4px;
    width: 4px;
    height: 4px;
}
.timeline:before {
    border: 1px solid;
    top: 25px;
}
.timeline-item-date {
    background: #a2a2a2;
    height: 32px;
}
.timeline-item-dateinner {
    height: 30px;
    line-height: 32px;
}
@media only screen and (max-width: 768px) {
    body {
        font: 16px sans-serif;
    }    
    .entry .title {
        width: 90%;
    }
    .entry .title:before {
        left: 6px;
    }
    .timeline-item-date {
        top: 0px;
    }
}
